@media screen and (min-width: 501px) {

}

@media (max-width: 500px) {
  .content {
    padding: 15px 0;
  }
  
  .container {
    padding: 0 30px;
  }
  
  .card {
    padding-left: 15px;
    padding-right: 15px;
  }
  
  .wrapper__body {
    padding: 30px 15px;
  }
  
  .credit-cell {
