    font-size: 14px !important;
    color: #646770 !important;
    margin-bottom: 5px !important;
    display: block !important;
    text-align: center;
  }
  
  .card__area input, .form__control {
    font-size: 12px;
  }
  
  .card__area img {
    width: 20px;
    right: 10px;
  }
  
  .credit-input {
    padding-left: 10px;
  }
  
  .form__btn {
    margin: 30px auto;
  }
  
  .price {
    font-size: 40px;
  }
  
  .oferta {
    font-size: 13px;
  }
}

@media (max-width: 350px) {
